import * as yup from 'yup';
export const validateEx1 = yup.object().shape({
  sum: yup
    .number()
    .typeError('  ')
    .required(' '),
  percent: yup
    .number()
    .typeError('  ')
    .required(' '),
  date: yup
    .date()
    .typeError('  ')
    .required(' '),
  month: yup
    .number()
    .typeError('  ')
    .max(100, '  100')
    .required(' '),
  period: yup
    .string()
    .typeError('  ')
    .required(' '),
});
