import { Button } from '@mui/material';
import { useFormik } from 'formik';
import React from 'react';
import { validateEx1 } from '../../../helpers/validate';
import ContainerCustom from '../../common/container';
import DateInput from '../../common/dateInput';
import InputCustome from '../../common/Input';
import SelectCustome from '../../common/select';

const ExOneApps = ({ className, setState }) => {
  const formik = useFormik({
    initialValues: {
      sum: '',
      percent: '',
      date: new Date(),
      month: '',
      period: '',
    },
    validateOnChange: true,
    validationSchema: validateEx1,
    onSubmit: (value) => {
      setState({
        ...value,
        period: {
          type: value.period,
          value:
            value.period === 'end'
              ? 1
              : value.period === 'month'
              ? +value.month ?? 0
              : Math.floor(+value.month / 12) * 12,
        },
      });
    },
  });
  return (
    <div className={`${className}__app1 app1`}>
      <ContainerCustom>
        <div className={`${className}__app1-content app1__content`}>
          <h2 className={`${className}__app1-title app1__title title`}>
              
          </h2>
          <form
            className={`${className}__app1-form app1__form `}
            onSubmit={formik.handleSubmit}
          >
            <div className={`${className}__app1-form app1__form-el `}>
              <InputCustome
                text={' '}
                id={'sum'}
                name={'sum'}
                state={formik.values.sum}
                setState={formik.handleChange}
                error={formik.touched.sum && Boolean(formik.errors.sum)}
                helperText={formik.touched.sum && formik.errors.sum}
              />
            </div>
            <div className={`${className}__app1-form app1__form-el `}>
              <InputCustome
                text={' ( )'}
                id={'month'}
                name={'month'}
                state={formik.values.month}
                setState={formik.handleChange}
                error={formik.touched.month && Boolean(formik.errors.month)}
                helperText={formik.touched.month && formik.errors.month}
              />
            </div>
            <div className={`${className}__app1-form app1__form-el `}>
              <InputCustome
                text={'  %'}
                id={'percent'}
                name={'percent'}
                state={formik.values.percent}
                setState={formik.handleChange}
                error={formik.touched.percent && Boolean(formik.errors.percent)}
                helperText={formik.touched.percent && formik.errors.percent}
              />
            </div>
            <div className={`${className}__app1-form app1__form-el `}>
              <DateInput
                id={'date'}
                name={'date'}
                state={formik.values.date}
                setState={formik.handleChange}
                error={formik.touched.date && Boolean(formik.errors.date)}
                helperText={formik.touched.date && formik.errors.date}
              />
            </div>
            <div className={`${className}__app1-form app1__form-el `}>
              <SelectCustome
                text={' '}
                month={formik.values.month}
                id={'period'}
                name={'period'}
                state={formik.values.period}
                setState={formik.handleChange}
                error={formik.touched.period && Boolean(formik.errors.period)}
                helperText={formik.touched.period && formik.errors.period}
              />
            </div>
            <div className={`${className}__app1-form app1__form-el `}>
              <Button
                type="submit"
                variant="contained"
                fullWidth
              >
                
              </Button>
            </div>
          </form>
        </div>
      </ContainerCustom>
    </div>
  );
};

export default ExOneApps;
