import React, { useEffect } from 'react';
import { useState } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { TableFooter, TablePagination } from '@mui/material';

export default function SpanningTable({ state = [] }) {
  const [data, setData] = useState([]);
  const [sum, setSum] = useState([]);
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  useEffect(() => {
    if (Object.entries(state).length) {
      setData(
        state?.all?.map((el, index) => {
          return {
            id: index,
            date: state?.period[index],
            percent: state?.depozit[index],
            sum: el,
          };
        })
      );
      setSum(state.depozit);
    } else {
      setData([]);
    }
  }, [state]);

  const sumResult = (sum) => {
    if (sum.length) {
      return sum.reduce((acc, el) => acc + el, 0);
    } else {
      return 0;
    }
  };

  // Avoid a layout jump when reaching the last page with empty rows.
  // const emptyRows =
  //   page > 0 ? Math.max(0, (1 + page) * rowsPerPage - data.length) : 0;

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  return (
    <TableContainer component={Paper}>
      <Table
        sx={{ minWidth: 700 }}
        aria-label="spanning table"
      >
        <TableHead>
          <TableRow>
            <TableCell>id</TableCell>
            <TableCell></TableCell>
            <TableCell> </TableCell>
            <TableCell> </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data
            .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            .map((row, index) => (
              <TableRow key={index}>
                <TableCell>{row.id}</TableCell>
                <TableCell>{row.date}</TableCell>
                <TableCell
                  sx={{
                    color: 'success.main',
                  }}
                >
                  {row.percent ? `+${row.percent}` : 0}
                </TableCell>
                <TableCell>{row.sum}</TableCell>
              </TableRow>
            ))}

          <TableRow>
            <TableCell colSpan={2}>:</TableCell>
            <TableCell align="right">{sumResult(sum)}</TableCell>
          </TableRow>
        </TableBody>
        <TableFooter>
          <TableRow>
            <TablePagination
              rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
              colSpan={4}
              count={data.length}
              rowsPerPage={rowsPerPage}
              page={page}
              labelRowsPerPage={':'}
              onPageChange={handleChangePage}
              onRowsPerPageChange={handleChangeRowsPerPage}
            />
          </TableRow>
        </TableFooter>
      </Table>
    </TableContainer>
  );
}
