import React from 'react';
import { useState } from 'react';
import TitleChapter from '../../common/chapter/titlechapter';
import InfoComponent from '../../common/InfoComponent';
import ExOneApps from '../../component/ExOneApps';
import ResultComponent from '../../component/Result';

const ExampleOnePage = () => {
  const [state, setState] = useState({
    sum: 10000,
    percent: 13,
    date: '2022-11-15',
    month: 12,
    period: {
      value: 12,
      type: 'year',
    },
  });
  return (
    <div className="ex1">
      <TitleChapter
        className={'ex1'}
        title={' 1'}
        about={'    '}
      />
      <InfoComponent
        className={'ex1'}
        setState={setState}
      />
      <ExOneApps setState={setState} />
      <ResultComponent state={state} />
    </div>
  );
};

export default ExampleOnePage;
