import { TextField } from '@mui/material';
import React from 'react';

const DateInput = ({ id, state, setState, name, error, helperText }) => {
  return (
    <>
      <TextField
        label=" "
        type="date"
        id={id}
        name={name}
        fullWidth
        InputLabelProps={{
          shrink: true,
        }}
        onChange={setState}
        value={state}
        error={error}
        helperText={helperText}
      />
    </>
  );
};

export default DateInput;
