import React from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Filler,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Filler,
  Tooltip,
  Legend
);

const GraphCompanentOne = ({ result }) => {
  if (result.show) {
    const options = {
      //   responsive: true,
      maintainAspectRatio: false,
      plugins: {
        legend: {
          position: 'top',
        },
        title: {
          display: true,
          text: ' ',
        },
      },
    };

    const labels = result.period;

    const data = {
      labels,
      datasets: [
        {
          fill: true,
          label: ' ',
          data: labels.map((_, index) => result.all[index]),
          borderColor: 'rgb(53, 162, 235)',
          backgroundColor: 'rgba(53, 162, 235, 0.5)',
        },
      ],
    };
    return (
      <>
        <Line
          options={options}
          data={data}
        />
      </>
    );
  } else {
    return <></>;
  }
};

export default GraphCompanentOne;
