import { FormControl, MenuItem, Select } from '@mui/material';
import InputLabel from '@mui/material/InputLabel';
import React from 'react';

const SelectCustome = ({
  month,
  name,
  id,
  state,
  setState,
  error,
  helperText,
}) => {
  const content = [
    {
      value: 1,
      text: '  ',
      type: 'end',
    },
    {
      value: +month ?? 0,
      text: '  ',
      type: 'month',
    },
    {
      value: Math.floor(month / 12) * 12,
      text: '  ',
      type: 'year',
    },
  ];

  return (
    <FormControl fullWidth>
      <InputLabel
        id={id}
        error={error}
      >
         
      </InputLabel>
      <Select
        name={name}
        label="Age"
        id={id}
        value={state}
        onChange={setState}
        error={error}
      >
        {content.map((el, index, arr) => (
          <MenuItem
            key={index}
            value={el.type}
          >
            {el.text}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};

export default SelectCustome;
