import { TextField } from '@mui/material';
import React from 'react';

const InputCustome = ({
  state,
  setState,
  text,
  id,
  name,
  error,
  helperText,
}) => {
  return (
    <>
      <TextField
        name={name}
        id={id}
        label={text}
        value={state}
        onChange={setState}
        fullWidth
        error={error}
        helperText={helperText}
      />
    </>
  );
};

export default InputCustome;
