import { TableHead } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';
import { Chart } from 'chart.js';
import React, { useEffect, useState } from 'react';
import { useRef } from 'react';
import { mathResult } from '../../../helpers/math';
import ContainerCustom from '../../common/container';
import GraphCompanentOne from '../../common/graph';
import GraphCompanentTwo from '../../common/graph/graphTwo';
import TableCustome from '../../common/table';

const ResultComponent = ({ state }) => {
  const [result, setResult] = useState([]);

  useEffect(() => {
    setResult(mathResult(state));
  }, [state]);

  return (
    <div>
      <ContainerCustom>
        {/* <pre>{JSON.stringify(state, null, 2)}</pre>
        <pre>{JSON.stringify(mathResult(state), null, 2)}</pre> */}
        <div style={{ width: '100%', marginBottom: '100px' }}>
          <TableCustome state={result} />
        </div>
        <div
          style={{
            display: 'grid ',
            gridTemplateColumns: '1fr 1fr',
            gridAutoRows: `400px`,
          }}
        >
          <div>
            {' '}
            <GraphCompanentOne result={result} />
          </div>

          <div>
            <GraphCompanentTwo result={result} />
          </div>
        </div>
      </ContainerCustom>
    </div>
  );
};

export default ResultComponent;
