export const mathResult = (state) => {
  try {
    if (state?.period?.type === 'end') {
      let now = new Date(state.date);
      const result = new Array(+state.month)
        .fill(1)
        .map(() => {
          const day = new Date(
            now.getFullYear(),
            now.getMonth() + 1,
            0
          ).getDate();
          const resultMas = Number(
            (+state.sum * +state.percent * day) / 365 / 100
          );
          now.setMonth(now.getMonth() + 1);
          return resultMas;
        })
        .reduce((acc, el) => acc + el, 0);
      return {
        all: [+state.sum, parseFloat(Number(+state.sum + result).toFixed(2))],
        depozit: [0, parseFloat(Number(result).toFixed(2))],
        period: [
          new Date(+state.date).toLocaleDateString(),
          now.toLocaleDateString(),
        ],
        show: true,
      };
    }
    if (state?.period?.type === 'month') {
      let now = new Date(state.date);
      let addNow = new Date(state.date);
      const masdate = [];
      const result = new Array(+state.month).fill(1).map(() => {
        const day = new Date(
          now.getFullYear(),
          now.getMonth() + 1,
          0
        ).getDate();
        const resultMas = parseFloat(
          Number((+state.sum * +state.percent * day) / 365 / 100).toFixed(2)
        );
        now.setMonth(now.getMonth() + 1);
        addNow = now.setMonth(now.getMonth());
        masdate.push(addNow);
        return resultMas;
      });

      return {
        all: [
          state.sum,
          ...result.reduce((acc, el, index) => {
            if (index === 0) {
              acc.push(parseFloat(Number(el + +state.sum).toFixed(2)));
              return acc;
            } else {
              acc.push(parseFloat(Number(acc[index - 1] + +el).toFixed(2)));
              return acc;
            }
          }, []),
        ],
        depozit: [0, ...result],
        period: [
          new Date(state.date).toLocaleDateString(),
          ...masdate.map((el) => {
            console.log();
            return new Date(el).toLocaleDateString();
          }),
        ],
        show: true,
      };
    }
    if (state?.period?.type === 'year') {
      let now = new Date(state.date);
      const result = new Array(+state.month)
        .fill(1)
        .map(() => {
          const day = new Date(
            now.getFullYear(),
            now.getMonth() + 1,
            0
          ).getDate();
          const resultMas = (+state.sum * +state.percent * day) / 365 / 100;
          now.setMonth(now.getMonth() + 1);
          return resultMas;
        })
        .reduce((acc, el) => acc + el, 0);
      return {
        all: [+state.sum, parseFloat(Number(+state.sum + result).toFixed(2))],
        depozit: [0, parseFloat(Number(result).toFixed(2))],
        period: [
          new Date(state.date).toLocaleDateString(),
          now.toLocaleDateString(),
        ],
        show: true,
      };
    }
    return {};
  } catch (e) {
    console.error(e);
    return {};
  }
};
